import React from 'react';
import './App.css';
import users from './api/users';
import UserList from './component/UserList';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loaded: false,
      loading: false,
      sortDirection: 'asc',
      users,

    };
  }

  render() {
    if (!this.state.loaded) {
      return (
        <button className="load_button" onClick={this.Loaded.bind(this)}>
          {this.state.loading ? 'Loading...' : 'Load'}
        </button>
      );
    }

    return (
      <div className="App">
        <h1>TO-DO LIST</h1>
        <div>
          <button
            className=" sort_button"
            onClick={(e) => {
              const newDirection = this.state.sortDirection !== 'asc' ? 'asc' : 'desc';
              this.setState({
                sortDirection: newDirection,
                users: this.state.users.sort((a, b) => {
                  if (a.name === b.name) {
                    return 0;
                  }
                  return a.name > b.name
                    ? newDirection === 'asc' ? 1 : -1
                    : newDirection === 'asc' ? -1 : 1;
                }),
              });
            }}
          >
Sort by name
            <img className={`arrow${this.state.sortDirection === 'desc' ? ' arrow_desc' : ''}`} src={require('./assets/sort-down.png')} />
          </button>
        </div>
        <UserList users={this.state.users} />
      </div>
    );
  }

  Loaded() {
    this.setState({ loading: true });
    setTimeout(() => {
      this.setState({ loading: false, loaded: true });
    }, 2000);
  }
}

export default App;
