import React from 'react';
import '../App.css';
import PropTypes from 'prop-types';
import todos from '../api/todos';
import TodoList from './TodoList';
import {todoItemShape} from './TodoItem';

class User extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sortDirection: 'asc',
      todos,
    };
  }

  render() {
    const userTodos = todos.filter(t => t.userId === this.props.user.id);

    return (
      <div className="user_block">
        <div className="user_info">
          <h2>{this.props.user.name}</h2>
          <h3>{this.props.user.email}</h3>

          <div className="user_location">
            <p>
              <strong>Street:</strong>
              {' '}
              {this.props.user.address.street}
            </p>
            <p>{this.props.user.address.suite}</p>
            <p>
              <strong>City:</strong>
              {' '}
              {this.props.user.address.city}
            </p>
          </div>
        </div>

        <TodoList todos={userTodos}/>
        <div>
          <button className={"buttonSortDone"} onClick={(e) => {
            const newDirection = this.state.sortDirection === 'asc' ? 'desc' : 'asc';
            this.setState({
              sortDirection: newDirection,
              todos: this.state.todos.sort((a, b) => {
                return (a.completed === b.completed)
                  ? 0
                  : a.completed
                    ? newDirection === "asc" ? -1 : 1
                    : newDirection === "asc" ? 1 : -1;
              })
            })
          }}>
            Sort by done
          </button>
        </div>
      </div>
    );
  }
}

const userShape = PropTypes.shape({
  userTodos: PropTypes.arrayOf(todoItemShape),
  name: PropTypes.string,
  id: PropTypes.number,
  email: PropTypes.string,
  address: PropTypes.shape({
    street: PropTypes.string,
    suite: PropTypes.string,
    city: PropTypes.string,
  }),
});

User.propTypes = {
  user: userShape.isRequired,
};

export default User;
export {userShape};
