import React from 'react';
import '../App.css';
import PropTypes from 'prop-types';


class TodoItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      done: props.todo.completed,
    };
  }

  render() {
    return (
      <div className="user_todo_block">
        <input type="checkbox" onChange={this.onDoneChanged.bind(this)} checked={this.state.done} />
        <p>{this.props.todo.title}</p>
      </div>
    );
  }

  onDoneChanged(e) {
    this.setState({ done: !this.state.done });
  }
}

const todoItemShape = PropTypes.shape({
  title: PropTypes.string,
  completed: PropTypes.bool,
});
TodoItem.propTypes = {
  todo: todoItemShape.isRequired,
};

export default TodoItem;
export { todoItemShape };
